import {RouteObject} from "react-router-dom";

import {Index as LogIndex} from "../../../page/admin/logs/Index"
import {Index as ServersIndex} from "../../../page/admin/servers/Index"
import {Index as FileIndex} from "../../../page/admin/file/Index"
import {PrivateRoute} from "../component/PrivateRoute";

export const adminRoute : RouteObject[] = [
    {
        path: "/admin/users",
        element: <></>
    },
    {
        path: "/admin/servers",
        element: <PrivateRoute chiledern={<ServersIndex/>}/>

    },
    {
        path: "/admin/logs",
        element: <PrivateRoute chiledern={<LogIndex/>}/>
    },
    {
        path: "/admin/file",
        element: <PrivateRoute chiledern={<FileIndex/>}/>
    }
]
