import {action, observable} from "mobx";
import {getAsync} from "../../../common/fetch/getAsync";
import {IIndex} from "./model/IIndex";
import {postAsync} from "../../../common/fetch/postAsync";
import {successToast} from "../../../common/toast/success";
import {closeLastModal} from "../../../common/component/modal/modalStore";
import {IAccessPosition} from "./model/IAccessPosition";

const state = observable({state: null as unknown as IIndex});
const accessPositionListState = observable({list: null as unknown as IAccessPosition[]})

const getIndexAsync = action(async() => {
    await getAsync("/User/GetIndex")
        .then(response => state.state = response.data);
})

const getAccessPositionsAsync = action(async() => {
    getAsync("/User/AccessPositions").then(response => {
        accessPositionListState.list = response.data.accessPositions;
    })
})

const changePasswordAsync = action(async (form: any) => {
    const md5 = require('md5');

    const afterPost = () => {
        closeLastModal()
        successToast("  .");
    }

    await postAsync<Object>("User/ChangePassword", {
        oldPassword: md5(form.oldPassword),
        password: md5(form.password)
    }, afterPost)
})

export {state, accessPositionListState, getIndexAsync, getAccessPositionsAsync, changePasswordAsync}
