import {FormProvider, useForm} from "react-hook-form";
import {Button, Form, FormControl, FormGroup, FormLabel} from "react-bootstrap";
import {sendFileAsync} from "../store";
import {TextInput} from "../../../../common/component/input/text/TextInput";
import {FileInput} from "../../../../common/component/input/file/FileInput";

export const FileLoader = () => {
    const hookForm = useForm();

    return <div>
        <FormProvider {...hookForm}>
            <Form onSubmit={hookForm.handleSubmit(sendFileAsync)} className="d-flex justify-content-around">
                <FileInput name="file"
                           isRequired={true}/>
                <TextInput name="tag"
                           placeholder=""
                           isRequired={true}/>
                <TextInput name="version"
                           placeholder=" "
                           isRequired={true}/>
                <FormGroup>
                    <Button type="submit"></Button>
                </FormGroup>
            </Form>
        </FormProvider>
    </div>
}
