import {Table} from "react-bootstrap";
import {observer} from "mobx-react-lite";
import {state} from "../store";

export const PaymentHistory = observer(() => {
    return <div>
        <div>
            <h6> :</h6>
        </div>

        {state.state?.payments &&
            state.state?.payments.map(payment => {
                return <div className="d-flex mb-2" key={payment.id}>
                    <div className="flex-fill">{payment.range}</div>
                    <div className="flex-fill">{new Date(payment.date).toLocaleDateString()}</div>
                    <div className="flex-fill">{new Date(payment.date).toLocaleDateString()}</div>
                </div>
            })
        }

        {!state.state?.payments &&
            <div>   .</div>
        }
    </div>
})
