import {FormProvider, useForm} from "react-hook-form";
import {Button, Form, ModalBody, ModalFooter, ModalHeader, ModalTitle} from "react-bootstrap";
import {observer} from "mobx-react-lite";
import {changePasswordAsync} from "../store";
import {PasswordInput} from "../../../../common/component/input/password/PasswordInput";
import {SetPasswordInputs} from "../../../../common/component/input/set-password/SetPasswordInputs";
import {closeLastModal} from "../../../../common/component/modal/modalStore";

export const ChangePassword = observer(() => {
    const hookForm = useForm({
        mode: "all"
    });

    return <>
            <FormProvider {...hookForm}>
                <Form onSubmit={hookForm.handleSubmit(changePasswordAsync)}>
                    <ModalHeader>
                        <ModalTitle> </ModalTitle>
                    </ModalHeader>
                    <ModalBody>
                        <PasswordInput
                            label=" :"
                            name="oldPassword"
                            placeholder="  "/>
                        <SetPasswordInputs/>
                    </ModalBody>
                    <ModalFooter>
                        <Button variant="success" type="submit"></Button>
                        <Button variant="danger" onClick={closeLastModal}></Button>
                    </ModalFooter>
                </Form>
            </FormProvider>
        </>
})
