import React, {useEffect} from 'react';
import {RouterProvider} from "react-router-dom";
import {Routing} from "./routing/Routing";
import 'react-toastify/dist/ReactToastify.css';
import {ToastContainer} from "react-toastify";
import {GoogleReCaptchaProvider} from "react-google-recaptcha-v3";
import {observer} from "mobx-react-lite";
import {getMainStore, state} from "./appStore";
import {ModalProvider} from "./component/modal/ModalProvider";

export const App = observer(() => {

  useEffect(() => {
    getMainStore()
  }, []);

  return <>
    {state.mainStore?.captchaPublicKey &&
        <GoogleReCaptchaProvider reCaptchaKey={state.mainStore?.captchaPublicKey}>
          <RouterProvider router={Routing}/>
          <ToastContainer/>
          <ModalProvider/>
        </GoogleReCaptchaProvider>
    }

    {!state.mainStore?.captchaPublicKey && (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') &&
        <>
          <RouterProvider router={Routing}/>
          <ToastContainer/>
          <ModalProvider/>
        </>
    }
  </>

})
