import {observer} from "mobx-react-lite";
import React from "react";
import {Link, useSearchParams} from "react-router-dom";
import {MainContent} from "./component/MainContent";
import {PrivacyPolicy} from "./component/PrivacyPolicy";
import {Button} from "react-bootstrap";
import {openModal} from "../../../component/modal/modalStore";
import {Contact} from "./component/Contact";

export const Index = observer(() => {
    const [searchParams, setSearchParams] = useSearchParams()
    const page = searchParams.get("page")

    return <>
        <header className="d-flex justify-content-around align-items-center border-bottom bg-black" style={{height: "50px"}}>
            <div className="d-none d-md-flex h-100">
                <Link to="/" className="d-flex align-items-center px-3 text-decoration-none text-white horizontal-menu-item">
                    
                </Link>
                <div className="d-flex align-items-center px-3 h-100 text-decoration-none text-white horizontal-menu-item"
                     style={{cursor: "pointer"}} onClick={() => setSearchParams("page=privacy_policy")}>
                     
                </div>
                <div className="d-flex align-items-center px-3 h-100 text-decoration-none text-white horizontal-menu-item"
                     style={{cursor: "pointer"}} onClick={() => setSearchParams("page=contacts")}>
                    
                </div>
            </div>

            <div className="d-flex h-100">
                <Link to="/" className="d-flex d-md-none align-items-center px-3 h-100 text-decoration-none text-white horizontal-menu-item">
                    
                </Link>
                <Link to="/register" className="d-flex align-items-center px-3 h-100 text-decoration-none text-white horizontal-menu-item">
                    
                </Link>
                <Link to="/login" className="d-flex align-items-center px-3 h-100 text-decoration-none text-white horizontal-menu-item">
                     
                </Link>
            </div>
        </header>
        <main className="d-flex flex-column justify-content-around align-items-center text-center">
            {!page && <MainContent/>}
            {page === "privacy_policy" && <PrivacyPolicy/>}
            {page === "contacts" && <Contact/>}
        </main>
        <footer className="d-md-none d-flex justify-content-center border-top border-1 bg-black" style={{height: "50px"}}>
            <div className="d-flex align-items-center px-3 h-100 text-decoration-none text-white horizontal-menu-item"
                 style={{cursor: "pointer"}} onClick={() => setSearchParams("page=privacy_policy")}>
                 
            </div>
            <div className="d-flex align-items-center px-3 h-100 text-decoration-none text-white  horizontal-menu-item"
                 style={{cursor: "pointer"}} onClick={() => setSearchParams("page=contacts")}>
                
            </div>
        </footer>
    </>
})
