import {action, observable} from "mobx";
import {postAsync} from "../../../common/fetch/postAsync";
import {successToast} from "../../../common/toast/success";
import {IFile} from "./model/IFile";
import {getAsync} from "../../../common/fetch/getAsync";

const state = observable({files: null as unknown as IFile[]});

const getFileList = action(async () => {
    getAsync("/admin/getfiles").then(response => {
        state.files = response.data.files;
    })
})

const sendFileAsync = action(async (form: any) => {
    const formData = new FormData();
    formData.append("file", form.file[0]);
    formData.append("tag", form.tag);
    formData.append("version", form.version);

    await postAsync("/Admin/UploadFile", formData, (response) => {
        if(response.status === 200){
            successToast("  .")
            getFileList()
        }
    })
})

export {state, getFileList, sendFileAsync}
