import { DoorOpenFill, InfoSquareFill, PersonSquare } from "react-bootstrap-icons";
import {removeToken} from "../../../store/auth";
import {Link} from "react-router-dom";

export const BottomMenu = () => {

    return <div className="d-flex d-lg-none justify-content-around border-2 border-top w-100" style={{height: "7vh", position: "fixed", bottom: 0, zIndex: "100", backgroundColor: "white"}}>
        <Link to="/" className="d-flex flex-fill justify-content-center align-items-center text-decoration-none text-black border-2 border-end p-2">
            <PersonSquare height="70%" width="70%"/>
        </Link>
        <Link to="/help" className="d-flex flex-fill justify-content-center align-items-center text-decoration-none text-black border-2 border-end p-2">
            <InfoSquareFill height="70%" width="70%"/>
        </Link>
        <div className="d-flex flex-fill justify-content-center align-items-center border-2 border-end p-2" onClick={removeToken}>
            <DoorOpenFill height="70%" width="70%"/>
        </div>
    </div>
}
