import {Badge, Button} from "react-bootstrap";
import {state} from "../store";
import {observer} from "mobx-react-lite";
import {openModal} from "../../../../common/component/modal/modalStore";
import {ChangePassword} from "./ChangePassword";
import {AccessPositions} from "./AccessPositions";
import {removeToken} from "../../../../common/store/auth";
import {Android2, Windows} from "react-bootstrap-icons";

export const Profile = observer(() => {
    return <>
        <div>
            <h6>:</h6>
        </div>
        <div className="d-block d-lg-flex justify-content-around">
            <div className="flex-fill">
                <div>
                    <span>:</span> {state.state?.login}
                </div>

                <div>
                    <span> : </span>{state.state?.email}
                </div>
            </div>
            <div className="flex-fill">
                <div>
                    <div>
                        <span> : </span>{new Date(state.state?.registerDate).toLocaleDateString()}
                    </div>
                    <span>: </span>

                    {new Date(state.state?.endAccessDate) > new Date() &&
                        <Badge bg="success">
                            ( {new Date(state.state?.endAccessDate).toLocaleDateString()})</Badge>
                    }

                    {new Date(state.state?.endAccessDate) <= new Date() &&
                        <Badge bg="danger">
                            ( {new Date(state.state?.endAccessDate).toLocaleDateString()})</Badge>
                    }
                </div>
            </div>
        </div>
        <div className="flex-column d-lg-flex justify-content-between mt-3">
            <div className="d-flex justify-content-around justify-content-lg-start">
                <Button variant="dark" size="sm" onClick={() => openModal(<ChangePassword/>)} className="me-2"> </Button>
                <Button variant="dark" size="sm" onClick={() => openModal(<AccessPositions/>)} className="me-2"> </Button>
                <Button variant="secondary" size="sm" onClick={removeToken} className="d-none d-lg-block me-2"></Button>
            </div>


            <div className="d-lg-none d-flex justify-content-around mx-4 mt-lg-0 mt-3">
                <a className="not-hover text-black me-3" href="https://lockvpn.me/files/android_app/app-release.apk">
                    <Android2 width={20} height={20}/>
                </a>
                <a className="not-hover text-black me-3" href="https://lockvpn.me/files/windowsapp/LockVPNSetup.msi">
                    <Windows width={20} height={20}/>
                </a>
            </div>
        </div>
        <hr/>
    </>
})
