import {action, observable} from "mobx";
import {IServer} from "../../model/IServer";
import {getAsync} from "../../../../../common/fetch/getAsync";

const state = observable({list: null as unknown as IServer[]});

const getServerListAsync = action(async () => {
    await getAsync("/Admin/GetServers").then(response => {
        state.list = response.data.servers;
    })
})

export {state, getServerListAsync}
