import {observer} from "mobx-react-lite";
import {useEffect} from "react";
import {getServerListAsync, state} from "./store";
import {Button} from "react-bootstrap";
import {PencilFill, Trash3Fill} from "react-bootstrap-icons";
import {openModal} from "../../../../../common/component/modal/modalStore";
import {AddEditServer} from "../add-edit-server/AddEditServer";

export const List = observer(() => {

    useEffect(() => {
        getServerListAsync();
    }, []);

    return <>
        <div className="d-flex mb-2">
            <div className="w-20"></div>
            <div className="w-20"></div>
            <div className="w-20">IP:PORT</div>
            <div className="w-20">- </div>
            <div className="w-20"></div>
        </div>
        {state.list?.map(server => {
            return <div className="d-flex mb-1">
                <div className="w-20">{server.id}</div>
                <div className="w-20">{server.name}</div>
                <div className="w-20">{server.ip}:{server.port}</div>
                <div className="w-20">{server.userCount}</div>
                <div className="w-20">
                    <Button className="me-2" onClick={() => openModal(<AddEditServer id={server.id}/>)}>
                        <PencilFill/>
                    </Button>
                    <Button variant="danger">
                        <Trash3Fill/>
                    </Button>
                </div>
            </div>
        })
        }
    </>

})
