import {observer} from "mobx-react-lite";
import {Button, ModalBody, ModalFooter, ModalHeader, ModalTitle} from "react-bootstrap";
import {closeLastModal} from "../../../../common/component/modal/modalStore";
import {useEffect} from "react";
import {accessPositionListState, getAccessPositionsAsync} from "../store";

export const AccessPositions = observer(() => {

    useEffect(() => {
        getAccessPositionsAsync();
    }, []);

    return <>
        <ModalHeader>
            <ModalTitle></ModalTitle>
        </ModalHeader>
        <ModalBody>
            <div className="d-flex flex-column">
                {
                    accessPositionListState.list?.map(position => {
                        return <div key={position.id} className="d-flex justify-content-around align-items-center mb-1">
                            <div className="w-25 mx-auto">
                                {position.name}
                            </div>
                            <div className="w-25 mx-auto">
                                {position.price}
                            </div>
                            <div className="w-25 mx-auto">
                                <a href={position.link} className="btn btn-success btn-sm">
                                    
                                </a>
                            </div>
                        </div>
                    })
                }
            </div>
        </ModalBody>
        <ModalFooter>
            <Button variant="danger" onClick={closeLastModal}></Button>
        </ModalFooter>
    </>
})
