import {Button, Form, ModalBody, ModalFooter, ModalHeader, ModalTitle} from "react-bootstrap";
import {closeLastModal} from "../../../../../common/component/modal/modalStore";
import {FormProvider, useForm} from "react-hook-form";
import {TextInput} from "../../../../../common/component/input/text/TextInput";
import {SelectInput} from "../../../../../common/component/input/select/SelectInput";
import {useEffect} from "react";
import {addServerAsync, getAddEditServerAsync, state} from "./store";
import {observer} from "mobx-react-lite";

export const AddEditServer = observer((props: {id: number}) => {
    const hookForm = useForm();

    useEffect(() => {
        getAddEditServerAsync(props.id)
    }, [props.id]);

    return <>
        <FormProvider {...hookForm}>
            <Form onSubmit={hookForm.handleSubmit((form) => addServerAsync(form))}>
                <ModalHeader>
                    <ModalTitle> </ModalTitle>
                </ModalHeader>
                <ModalBody>
                    <TextInput name="name"
                               label=" :"
                               isRequired={true}/>
                    <SelectInput name="country"
                                 label=":"
                                 isRequired={true}
                                 placeholder=" "
                                 options={state.form?.countries}/>
                    <TextInput name="ip"
                               label="IP :"
                               isRequired={true}/>
                    <TextInput name="port"
                               label=":"
                               isRequired={true}/>
                    <TextInput name="description"
                               label=":"
                               isRequired={true}/>
                    <TextInput name="userName"
                               label=" :"
                               isRequired={true}/>
                    <TextInput name="password"
                               label=":"
                               isRequired={true}/>
                </ModalBody>
                <ModalFooter>
                    <Button variant="dark" type="submit"></Button>
                    <Button variant="secondary" onClick={closeLastModal}></Button>
                </ModalFooter>
            </Form>
        </FormProvider>

    </>
})
