import {action, observable} from "mobx";
import {ReactNode} from "react";

let state = observable({modal: new Array<ReactNode>()})

const openModal = action((modal: ReactNode) => {
    console.log("ok")
    state.modal.push(modal)
})

const closeLastModal = action(() => {

    state.modal = state.modal.slice(0, -1)
})

export {state, openModal, closeLastModal}
